import axios, { type AxiosResponse } from 'axios'

import type { ClubId } from 'src/models/Club'
import { type Semester } from 'src/models/date'

interface PatchParameters {
  clubId: ClubId
  data: SemesterBody
  token: string
}

interface SemesterBody {
  year: number
  semester: Semester
}

export const patchChangeSemester = async ({ clubId, token, data }: PatchParameters) => {
  await axios.patch<null, AxiosResponse<null>, SemesterBody>(`${process.env.REACT_APP_APIHOST}/club/${clubId}`, data, {
    headers: { Authorization: token }
  })
}
